@import url('https://fonts.googleapis.com/css?family=Noto+Serif+TC:400,700');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600');
html, body{
    font-family: "Noto Serif TC", "Microsoft JhengHei", "HanHei TC", "MHei", "PingFang TC", "STHeitiTC-Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
    background-color: #EBEBEC;
}
input, textarea , button, submit {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: none;
    border: none;
}
textarea {
    resize: none;
}
a {
  color: inherit;
  text-decoration: inherit;

}
ul {
    /* list-style-type: none; */
    margin: 0;
    /* padding: 0; */
}
*{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-repeat: no-repeat;
    background-position: center center;
    margin: 0;
    padding: 0;
}
.page{
    padding: 98px 40px 0 40px;
    margin-bottom: 80px;
}
@media (min-width: 768px){
    .page{
        padding: 120px 80px 0 80px;
        margin-bottom: 80px;
    }
}
@media (min-width: 1024px){
    .page{
        padding: 152px 0px 0 0px;
        width: calc(100vw - 160px);
        max-width: 1542px;
        margin: auto;
    }
}


