@media (min-width: 768px){
    section.about_page{
        width: calc(100vw - 160px);
        max-width: 674px;
    }
}
@media (min-width: 1024px){
    section.about_page{
        width: 600px;
        margin: auto;
    }
}
section.about_page .illustraion{
    margin-bottom: 24px;
}
@media (min-width: 1024px){
    section.about_page .illustraion{
        margin-bottom: 80px;
    }
}
section.about_page .illustraion p{
    font-size: 14px;
    line-height: 20px;
    color: #747174;
}
@media (min-width: 1024px){
    section.about_page .illustraion p{
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 1px;
    }
}
section.about_page .contact .content h1, section.about_page .contact .content span{
    font-family: 'Open Sans', sans-serif;
    color: #747174;
    font-size: 14px;
    line-height: 20px;
}
@media (min-width: 1024px){
    section.about_page .contact .content h1, section.about_page .contact .content span{
        font-size: 16px;
        line-height: 24px;
    }
}
section.about_page .contact .content h1{
    margin: 0;
}


