.header{
    position: fixed;
    z-index: 100;
}
@media (min-width: 1024px){
    .header{
        width: 100vw;
        height: 150px;
        background-color: #EBEBEC;
    }
}
.header .container{
    position: relative;
    width: 100vw;
    height: 98px;
    z-index: 10;
    padding: 24px 40px;
    background-color: #EBEBEC;
}
@media (min-width: 768px){
    .header .container{
        height: 80px;
        padding: 48px 80px;
    }
}
@media (min-width: 1024px){
    .header .container{
        width: 70px;
        height: auto;
        padding: 0;
        position: absolute;
        top: 80px;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
}
.header .container .logo{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.header .container .logo img{
    width: 50px;
}
@media (min-width: 768px){
    .header .container .logo img{
        width: 60px;
    }
}
@media (min-width: 1280px){
    .header .container .logo img{
        width: 70px;
    }
}
.header .container .hamburger{
    width: 40px;
    height: 40px;
    padding: 10px;
    position: relative;
    position: absolute;
    top: 50%;
    right: 40px;
    -webkit-transform: translate(0%, -50%);
            transform: translate(0%, -50%);
    cursor: pointer;
}
@media (min-width: 768px){
    .header .container .hamburger{
        right: 80px;
    }
}
@media (min-width: 1024px){
    .header .container .hamburger{
        display: none;
    }
}
.header .container .hamburger span{
    width: 40px;
    height: 2px;
    background-color: #747174;
    display: block;
    position: relative;
    top: 9px;
    -webkit-transition: .5s;
    transition: .5s;
}
.header .container .hamburger span:before, .header .container .hamburger span:after{
    content: '';
    width: 40px;
    height: 2px;
    background-color: #747174;
    display: block;
    position: relative;
    -webkit-transition: .5s;
    transition: .5s;
}
.header .container .hamburger span:before{
    top: -9px;
}
.header .container .hamburger span:after{
    top: 7px;
}
.header .container .hamburger.visible span{
    width: 0;
}
.header .container .hamburger.visible span:before{
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transition: .5s;
    transition: .5s;
    -webkit-transform-origin: left;
            transform-origin: left;
    width: 26px;
}
.header .container .hamburger.visible span:after{
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    -webkit-transition: .5s;
    transition: .5s;
    -webkit-transform-origin: left;
            transform-origin: left;
    width: 26px;
}
.header .menu{
    list-style: none;
    background-color: #EBEBEC;
    width: 100vw;
    height: calc(100vh - 98px);
    padding: 0 30px 98px 30px;
    position: relative;
    top: -90px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    top: -100vh;
    -webkit-transition: .5s;
    transition: .5s;
}
@media (min-width: 768px){
    .header .menu{
        height: 200px;
        top: -200px;
        padding: 0 0 24px 0;
    }
}
@media (min-width: 1024px){
    .header .menu{
        -moz-flex-direction: row;
        -ms-flex-direction: row;
        -o-flex-direction: row;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
                flex-direction: row;
        height: auto;
        width: auto;
        padding: 0;
        background-color: transparent;
        opacity: 1;
        position: absolute;
        top: 80px;
        right: 80px;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
    }
}
.header .menu li{
    font-size: 5vw;
    font-weight: bold;
    color: #747174;
    margin-bottom: 32px;
}
@media (min-width: 1024px){
    .header .menu li{
        font-size: 18px;
        margin: 0 40px 0 0;
    }
}
@media (min-width: 1024px){
    .header .menu li:last-child{
        margin: 0;
    }
}
.header .menu.visible{
    top: 0;
    -webkit-transition: .5s;
    transition: .5s;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
@media (min-width: 1024px){
    .header .menu.visible{
        top: 80px;
    }
}


